import { mapState } from 'vuex'
export default{
	components:{
		
	},
	data(){
		return {
			information:{},
			payWayId:null,
			payWayList:[
				{
					icon:'icon-weixin',
					title:'微信支付',
					id:1
				},
				{
					icon:'icon-zhifubao',
					title:'支付宝支付',
					id:2
				},
				{
					icon:'icon-downLoad',
					title:'下载招考宝APP支付',
					id:3
				}
			]
		}
	},
	computed:{
		...mapState(['userInfo'])
	},
	onLoad(){
		//编辑信息页传过来的参数
		uni.$on('information',data => {
			console.log(data,'aa')
			this.information = data
		})
	},
	onUnload(){
		//移除监听
		uni.$off('information')
	},
	methods:{
		//去编辑个人信息
		editInfo(){
			uni.navigateTo({
				url:`/sub_user/pages/personalInfo/info-edit?back=${true}`
			})
		},
		//选择支付方式 微信 支付宝
		payWayClick(index){
			this.payWayId = index
		},
		//说明点击
		summaryClick(){
			uni.showModal({
				title:'预报读费用说明',
				content:"1、缴纳预报读费用后学院会为您保留学位，入学时抵扣相应金额的报名费用。\n 2、如若报读申请未通过校方审核的，当前费用也将全数退还",
				showCancel:false,
				success: function (res) {}
			})
		},
		//标签点击
		labelClick(){
			uni.showToast({
			    title: '当前院校已加入平台助学计划',
				icon:'none',
				position:'top',
			    duration: 3000
			});
		},
		//助学计划
		planClick(){
			uni.navigateTo({
				url:'/sub_school/pages/apply/summary'
			})
		},
		//转账支付
		transferAccounts(){
			uni.navigateTo({
				url:'/sub_school/pages/apply/transferAccounts'
			})
		}
	}
}
